header{position:sticky;z-index:999;display:flex;align-items:center;justify-content:center;top:0;left:0;width:100%;background:var(--background-color);will-change:transform;transition:transform .4s ease}header nav{display:flex;align-items:center;justify-content:space-between;width:100%;max-width:var(--body);padding:1.25rem var(--margin)}header nav .menu{display:flex;align-items:center;gap:1.5rem}