section{position:relative;z-index:1;display:grid;grid-template-columns:repeat(2, 1fr);align-items:flex-start;-moz-column-gap:1rem;column-gap:1rem;width:100%;padding:4rem var(--margin);background:var(--background-color)}section:nth-of-type(2){padding-top:unset}section.-cover{position:sticky;z-index:-1;display:flex;align-items:center;top:0;left:0;width:100%;padding:unset}section.-cover video{background-image:unset;outline:unset;-o-object-fit:cover;object-fit:cover;filter:brightness(80%)}section.-hidden{overflow:hidden}section.-large{padding-top:8rem;padding-bottom:8rem}@media screen and (max-width: 820px){section{grid-template-columns:100%}section.-large{padding-top:4rem;padding-bottom:4rem}}