.numeric{display:flex;flex-direction:column;justify-content:flex-end;gap:1rem}.numeric.-unit div span:last-of-type{margin-bottom:.375em;font-size:var(--headline-size);font-weight:var(--headline-weight);line-height:var(--headline-height);letter-spacing:var(--headline-spacing)}.numeric div{display:flex;align-items:flex-end;padding-bottom:1rem;border-bottom:2px solid var(--foreground-color)}.numeric div span{font-weight:500;line-height:1}.numeric div span:first-of-type,.numeric div span:last-of-type{font-size:5rem;font-weight:var(--display-weight);line-height:var(--display-height);letter-spacing:var(--display-spacing)}